import apiList from './api/index';


const install = (Vue: any): any => {
    // @ts-ignore
    if (install.installed) {
        // @ts-ignore
        return install.installed = true;
    }
    /*Vue*/
    Object.defineProperties(Vue.prototype, {
        $api: {
            get() {
                return apiList;
            },
        },
    });
};
export default {
    install,
};

