/*  */
import * as Mutation from '../mutations';

// user
const state = {
    user: {},
};

// mutationsstateMutation 
const mutations = {
    /*  mutation  (type)    (handler) */
    //  state 

    /*  Mutation  */
    [Mutation.SET_USER_INFO]: (state: any, user: any) => {
        state.user = user;
    },
};

/* Action  mutation */
// Action  mutation
// Action 
const actions = {
    // Action  store  context  context.commit  mutation

    /* es6context */
    setUserInfo: ({commit}: any, user: any) => {
        commit(Mutation.SET_USER_INFO, user);
    },
};


export default {
    state,
    mutations,
    actions,
};
